import React from 'react';
import {inject, observer} from 'mobx-react';
import store from '../store';
import ReactMarkdown from 'react-markdown';

@inject((props)=>{
  return({
    ...props,
    uiStore: store
  })
})

@observer
class Article extends React.Component {
  componentDidMount() {
    const {uiStore} = this.props;
    let MdFile = require(`~/assets/markdown/${uiStore.currentArticle}.md`);
    fetch(MdFile)
      .then(res => res.text())
      .then(text => uiStore.setCurrentArticleContent(text));
  }

  render() {
    const {uiStore}= this.props;
    return (
      <article id="article">
        <h2 className="major">{uiStore.currentArticle}</h2>
        <ReactMarkdown
          className = 'article-content'
          source = {uiStore.currentArticleContent}
          escapeHtml={false}
        />
        <div className='close' onClick={this.goToHomePage}>Close</div>
      </article>
    );
  }

  goToHomePage = () => {
    this.props.history.push('/articles');
  }
}

export default Article;